  height: 10rem;
  p {
    margin: 0.4rem 0;
    color: #999999;
    font-size: 1.2rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .el-button {
    width: 10rem;
  }
}
.player {
  height: 60rem;
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 1rem;
  .dplayer /deep/ {
    width: 96rem;
    height: 54rem;
    .dplayer-danmaku-item {
      font-size: 2.5rem;
      font-weight: bold;
    }
  }
}
.zan {
  padding: 2rem 0;
  width: 100%;
  i:before {
    font-size: 2.5rem;
